$globalSvgIconImgPath : "../../img/global/svg";

.creditcardbuy{

  &-section{
    background-color: $global-clr-white;
    padding: 70px 0;

    @include ReSassWidthResponsive(1400){
      padding: 40px 0;
    }
    @include ReSassWidthResponsive(768){
      padding: 20px 0;
    }
    @include ReSassWidthResponsive(576){
      padding: 15px 0 20px;
    }

    > .container{
      max-width: 900px;
    }
  }

  &-form{

    .formuinew{
      flex-direction: column;

      label{
        font-weight: 500;
      }

      .label-flex{
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include ReSassWidthResponsive(768){
          margin-bottom: 6px;
        }

        > *{
          margin: 0 !important;
          padding: 0 !important;
        }
      }

      div[class*="row"]{
        @include ReSassWidthResponsive(768){
          display: block !important;
        }
      }

      .form-group{
        margin-bottom: 20px;

        @include ReSassMinWidthResponsive(768){
          margin-bottom: 0 !important;
        }
      }

      .form-group.fontsize20px div.form-control{
        padding-top: 9px;
        padding-bottom: 9px;
      }

      .autoreadonly{
        &.disabled,
        &:disabled,
        &[readonly] {
          color: $global-clr-dark-blue !important;
          background-color: $global-clr-white !important;
          border-color: #ced4da !important;
        }
      }

      .autoreadonlyload{
        position: absolute;
        display: block;
        right: 110px;
        width: 24px;
        height: 24px;
        opacity: 0;
        top: 50%;
        @include transform-multiple(translateY(-50%) scale(0));
        @include transition-multiple(opacity 0.1s ease-in-out, transform 0.1s ease-in-out);
      }

      .autoreadonly[readonly] + .autoreadonlyload{
        opacity: 1;
        @include transform-multiple(translateY(-50%) scale(1));
        z-index: 3;
      }

      .form-group-inputinfotext{
        font-size: 17px;
        margin-top: 2px;

        @include ReSassWidthResponsive(480){
          font-size: 15px;
        }
      }

      .infototal{
        padding-left: 12px;
        position: relative;

        &::before{
          content: "";
          background-image: url("data:image/svg+xml;base64,");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          width: 8px;
          height: 8px;
          position: absolute;
          left: 0;
          top: 7px;
        }
      }

      .formiconconvert{
        position: relative;
        width: 40px;

        @include ReSassWidthResponsive(768){
          width: 100%;
          height: 30px;
          margin-bottom: 10px;
        }

        div.iconconvert{
          display: flex;
          width: 30px;
          height: 100%;
          position: absolute;
          bottom: 0;

          @include ReSassWidthResponsive(768){
            width: 30px;
            height: 30px;
            margin: auto;
            position: static;
            display: block;
          }
        }

        i.iconconvert{
          width: 30px;
          height: 30px;
          background-image: url("data:image/svg+xml;base64,");
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
          display: block;
          margin: auto;

          @include ReSassWidthResponsive(768){
            @include rotate(90);
          }

          @include ReSassWidthResponsive(480){
            width: 20px;
            height: 20px;
          }
        }
      }

      .custom-radio{
        .custom-control-input:focus ~ .custom-control-label::before {
          box-shadow:none !important;
        }

        &.paymentcheckbox{
          min-height: 60px;
          padding-left: 0;
          padding-right: 60px;
          @include border-radius(4px);
          //overflow: hidden;

          .custom-control-label{
            background-color: $global-clr-light-gray;
            width: 100%;
            height: 60px;
            @include border-radii(3px,0,0,3px);
            z-index: 1;

            &::before,
            &::after{
              left: auto;
              @include transition-multiple(all 0.1s ease-in-out);
            }
            &::before{
              width: 60px;
              height: 62px;
              right: -60px;
              top: -1px;
              background-color: $global-clr-soft-gray;
              @include border-radii(0,3px,3px,0);
            }
            &::after{
              width: 14px;
              height: 14px;
              border-bottom: 2px solid #fff;
              border-left: 2px solid #fff;
              right: -35px;
              top: 22px;
              @include rotate(-135);
            }
          }

          .custom-control-input{
            width: 60px;
            height: 60px;
            right: 0;
            left: auto;
            z-index: 1;

            &:checked ~ .checkboxborder{
              border-color: $global-clr-soft-blue;
            }

            &:checked ~ .custom-control-label::before{
              background-color: $global-clr-soft-blue;
            }

            &:checked ~ .custom-control-label::after{
              width: 20px;
              height: 10px;
              right: -41px;
              top: 22px;
              @include rotate(-45);
            }
          }

          .checkboxborder{
            width: calc(100% + 1px);
            height: 62px;
            display: block;
            position: absolute;
            z-index: 0;
            top: -1px;
            left: -1px;
            border: 1px solid transparentize($global-clr-soft-gray, 0.7);
            @include border-radius(4px);
          }
        }

        .paymentmethod{
          color: $global-clr-dark-blue;
          font-size: 17px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: 0;
          padding: 10px;

          @include ReSassWidthResponsive(400){
            display: block !important;
          }

          &-text{
            display: flex;
            flex-direction: row;
            align-items: center;
            font-weight: 500;
          }

          &-info{
            height: 35px;

            @include ReSassWidthResponsive(400){
              margin-top: 10px;
            }
          }

          .iconselectpayment{
            width: 35px;
            height: 35px;
            display: inline-block;
            margin-right: 8px;

            @include ReSassWidthResponsive(400){
              width: 28px;
              height: 28px;
              margin-right: 5px;
            }
          }

          .iconinfopayment{
            height: 35px;
            display: inline-block;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;

            @include ReSassWidthResponsive(400){
              height: 20px;
            }

            &-visa{
              width: 54px;
              background-image: url("data:image/svg+xml;base64,");
            }
            &-master{
              width: 44px;
              background-image: url("data:image/svg+xml;base64,");
            }
            &-unionpay{
              width: 44px;
              background-image: url("data:image/svg+xml;base64,");
            }
          }
        }
      }

      .hrline{
        width: 100%;
        height: 15px;
        border: none;
        position: relative;
        margin: 35px 0 25px;

        &::before,
        &::after{
          content: "";
          position: absolute;
          z-index: 0;
        }

        &::before{
          width: 100%;
          height: 1px;
          left: 0;
          top: 0;
        }

        &::after{
          width: 14px;
          height: 14px;
          background-color: $global-clr-white;
        }

        &-graydownarrow{
          &::before{background-color: transparentize($global-clr-soft-gray, 0.7);}
          &::after{
            background-color: $global-clr-white;
            border-right: 1px solid transparentize($global-clr-soft-gray, 0.7);
            border-bottom: 1px solid transparentize($global-clr-soft-gray, 0.7);
            top: -6px;
            left: 50%;
            @include transform-multiple(translateX(-50%) rotate(45deg));
          }
        }
      }

      .formbottombtn{
        .btn{
          width: 100%;
          font-size: 20px;
          display: block;
          margin: auto;
          padding-top: 3px;

          @include ReSassWidthResponsive(768){
            width: 100%;
          }
        }
      }
    }

    .creditcardsummary{
      padding: 20px;
      border-left: 1px solid darken($global-clr-light-gray, 10%);
      border-right: 1px solid darken($global-clr-light-gray, 10%);

      .boxcolumn-boxtitle{
        margin-bottom: 15px;

        .boxcolumn-boxtitle-title{
          font-size: 17px;
        }

        .sitecolorgray{
          font-size: 17px;
          line-height: 1;
        }
      }

      &-row{
        margin-bottom: 20px;

        .paymentsummaryinfo{
          border: 1px solid transparentize($global-clr-soft-gray, 0.7);
          @include border-radius(3px);

          &-head{
            color: $global-clr-dark-blue;
            background-color: $global-clr-light-gray;
            font-size: 17px;
            font-weight: 500;
            padding: 10px 15px;
            margin: 0;
          }
          &-list{
            margin: 15px;

            li{
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              color: $global-clr-dark-blue;
              font-size: 17px;
              padding: 5px 0;
              border-bottom: 1px solid transparentize($global-clr-soft-gray, 0.7);

              &:first-child{
                padding-top: 0;
              }

              &:last-child{
                padding-bottom: 0;
                border-bottom: none;
              }
            }
          }
        }

        .smallinfotext{
          display: block;
          color: $global-clr-soft-gray;
          font-size: 14px;
          margin-top: 4px;
        }

        .paymentsummaryterm{

          @include ReSassWidthResponsive(768){
            margin-top: 15px;
          }

          p, label{
            color: $global-clr-dark-blue;
            font-size: 17px;
            line-height: 1.25;
          }

          p{
            margin-top: 10px;

            &:first-child{margin-top: 0;}
          }

          .form-check{
            margin-top: 15px;
          }
        }
      }
    }

  }

  &-assets{
    background-color: $global-clr-light-gray;
    padding: 10px 0;

    .creditcardassets{

      &.row > div[class*="col"]{
        padding-left: 10px;
        padding-right: 10px;

        &:first-child{
          padding-left: 15px;
        }
        &:last-child{
          padding-right: 15px;
        }

        @include ReSassWidthResponsive(768){
          padding-left: 15px !important;
          padding-right: 15px !important;
        }
      }

      &-big{
        &-pie{
          width: 230px;
          height: 230px;
          padding: 40px 0;
          position: relative;

          @include ReSassWidthResponsive(768){
            margin: 0 auto 15px;
          }
        }

        &-text{
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          text-align: center;
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          font-size: 17px;
          line-height: 1.1;

          strong, span{
            display: block;
            font-weight: 500;
          }

          strong{
            color: $global-clr-dark-blue;
            font-size: 20px;
          }

          span{
            color: $global-clr-soft-gray;
          }
        }
      }

      &-small{

        .boxcolumn-headtitle{
          border-bottom: 1px solid transparentize($global-clr-soft-gray, 0.7);
          padding-bottom: 5px;

          .iosstyletogglebutton{
            .ioscheckbox{
              display: flex;
              align-items: center;

              span{
                color: $global-clr-soft-gray;
                font-size: 17px;
                display: inline-block;
                margin-left: 5px;
                top: -2px;
                position: relative;
              }
            }
          }
        }

        &-list{
          flex-wrap: wrap;

          &-item{
            width: 20%;
            margin: 10px 0;

            @include ReSassWidthResponsive(1200){
              width: 25%;
            }
            @include ReSassWidthResponsive(768){
              width: calc(100% / 3);
            }
            @include ReSassWidthResponsive(480){
              width: calc(100% / 2);
            }
          }
        }

        &-pie{
          width: 40px;
          height: 40px;
          margin-right: 7px;
        }

        &-text{
          color: $global-clr-dark-blue;
          font-size: 17px;
          line-height: 0.9;

          *{display: block;}
        }
      }
    }
  }

}